/*********************************************
	* FONTS 
	* List all your custom fonts here
	* each in their own parenthesis
	*
	*
	* 1) $font_family
	* 2) $file_name
	* 3) Weight
	* 4) Style

*********************************************/

$fonts: 
	('Lato' Lato-Regular normal normal),
	('Lato' Lato-Italic normal italic),
	('Lato' Lato-Heavy 900 normal),
	('Lato' Lato-HeavyItalic 900 italic),
	('Lato' Lato-Semibold bold normal),
	('Lato' Lato-SemiboldItalic bold italic),
	('Lato' Lato-Light 300 normal),
	('Lato' Lato-LightItalic 300 italic),
	('Fakt' FaktPro-Light normal normal),
	('Fakt' FaktPro-LightItalic normal italic),
	('Fakt' FaktPro-Thin 300 normal),
	('Fakt' FaktPro-ThinItalic 300 italic),
	('Fakt' FaktPro-Bold bold normal),
	('Fakt' FaktPro-BoldItalic bold italic)
;

/*********************************************
	*CREATE THE @FONT-FACE DECLARATIONS
*********************************************/
@each 
	$font_family, 
	$file_name, 
	$weight, 
	$style 
	
	in $fonts {

	@font-face {
		font-family: $font_family;
		src: url('../fonts/#{$file_name}.eot');
		src: url('../fonts/#{$file_name}.eot?#iefix') format('embedded-opentype');
		src: url('../fonts/#{$file_name}.woff') format('woff'), 
			 url('../fonts/#{$file_name}.woff') format('woff'), 
		     url('../fonts/#{$file_name}.ttf')  format('truetype'); 
		font-weight: $weight;
		font-style: $style;     
	}
	
}