//Backdrop
.featured-backdrop.backdrop:after {
	z-index: 2;
	@include media(">md") {
		right: 	- $spacer * 2;
		bottom: - $spacer * 3;
		width: 90vw;
		left: auto;

	}
	@include media("<md") {
		left: 0;
		right: 0;
		bottom: 0;
		width: 100vw;
	}
}

