//slides
.featured-slides {
	.slick-next, .slick-prev {
		bottom: - $spacer * 2;

		@include media("<sm") {
			display: none;
		}
	}

	.slick-next {
		right: $spacer * 5;
	}

	.slick-prev {
		right: $spacer * 8;
	}
}

.slide-wrap-link {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: 5;
}

//Image
.featured-slide-image {
	width: 100%;
	max-height: 80vh;
	overflow: hidden;
}

//Header
.featured-slide-header {
	//bottom: $spacer * 2;
	bottom: 1vw;
	right: $spacer * 4;
	width: 90%;
	max-width: 600px;
	z-index: 3;

	p,
	.featured-slide-title {
		color: $white;
	}

	.featured-slide-title {
		line-height: 1;
		font-size: 5vw;
		margin-bottom: 1vw;
	}

	.btn {
		background: currentColor;
		color: $white;
	}
}