// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.

$spacer: $grid-gutter-width/2 !default;
$spacers: (
  0: 0,
  12: $spacer/2,
  13: $spacer/3,

  -1: -$spacer,
  -2: -($spacer * 2),
  -3: -($spacer * 3),
  -4: -($spacer * 4),
  -5: -($spacer * 5),
  -6: -($spacer * 6),
  -7: -($spacer * 7),
  -8: -($spacer * 8),    
  -9: -($spacer * 9),

  1: $spacer,
  2: ($spacer * 2),
  3: ($spacer * 3),
  4: ($spacer * 4),
  5: ($spacer * 5),
  6: ($spacer * 6),
  7: ($spacer * 7),
  8: ($spacer * 8),    
  9: ($spacer * 9),

  25: 25%,
  50: 50%,
  75: 75%,
  100: 100%,
  
  -25: -25%,
  -50: -50%,
  -75: -75%,
  -100: -100%

) !default;

// This variable affects the `.h-*` and `.w-*` classes.
$sizes: (
  1: $spacer,
  2: ($spacer * 2),
  3: ($spacer * 3),
  4: ($spacer * 4),
  5: ($spacer * 5),
  25: 25%,
  30: 30%,
  50: 50%,
  75: 75%,
  100: 100%
) !default;