.nav-primary {

	li {
		font-family: $font-family-sans-serif;
		font-weight: bold;
		margin-left: $spacer;

		&.active a {
			color: $blue;
		}

		@include media("<lg") {
			margin-left: 0;
		}
	}

	a {
		color: $gray-600;
		padding: $spacer/2;

		&:hover {
			color: $blue;
		}
	}

	//Donate
	li.menu-donate a {
		background: $orange;
		color: $white;

		&:hover {
			background: $blue-light;
			color: $white;
		}
	}
}