.gform_wrapper {

	.gform_body {

		input,
		textarea {
			@extend .form-control;
			padding-left: 0 !important;
			margin-bottom: $spacer;
			width: 100% !important;
			font-size: $h6-font-size !important;
		}

		label {
			display: none !important;
		}

		textarea.large {
			height: 200px;
		}


		//Fixes
		ul.gform_fields {
			position: relative;
			text-align: justify;
			
			&:after {
				position: relative;
				display: inline-block;
				width: 100%;
				height: 0;
				line-height: 0;
				content: '';	
			}
			
			& > * {
				display: inline-block;
				width: 100%;
				margin-top: 0 !important;
				margin-bottom: $spacer/2;
			}
			
			#field_1_2,
			#field_1_3 {
				width: 50%;
			}

		}
	}

	.gform_footer.top_label {
		margin-top: 0;
		padding-top: 0;

		input[type="submit"] {
			@extend .btn;
			@extend .btn-primary;
			@extend .text-uppercase;
			@extend .w-50;
		}

	}

}

//FUCK GRAVITY FORMS
//
body .gform_wrapper .top_label div.ginput_container {
	margin-top: 0 !important;
}


//Errors
div.gform_wrapper.gform_validation_error form {
	
	//Errors
	//
	li.gfield.gfield_error {
		background: transparent !important;
		border-top: 0 !important;
		border-bottom: 0 !important;
	}

	div.validation_error {
		text-align: left;
		border-bottom: 0 !important;
		border-top: 0 !important;
		font-family: $font-family-sans-serif;
		color: $red;
		text-transform: uppercase;
		font-weight: normal;
		margin-bottom: $spacer;
		padding-bottom: 0;
		padding-top: 0;
		font-size: $h6-font-size;
	}

	.gfield_description.validation_message {
		display: none;
	}

	.instruction.validation_message {
		text-transform: uppercase;
		color: $red;
		margin-top: $spacer/3;
		font-size: $font-size-sm;
		font-weight: normal;
	}

	.gfield_error {
		input[type=text],
		input[type=textarea],
		textarea {
			border: 0 !important;
			border-bottom: 2px solid $red !important;
		}
	}

	img.gform_ajax_spinner {
		display: none;
	}

	//FUCK GRAVITY
	//
	ul.gform_fields li.gfield.gfield_error:not(.gf_left_half):not(.gf_right_half) {
		max-width: 100% !important;
	}

}


//Success
//
.gform_confirmation_message {
	@extend .h4;
	color: $blue;
}