//Header & Image positioning
//
.content-project-header {
	z-index: 2;

	.content-project-title {
		line-height: 1;
	}
}

.content-project-image {
	@include media(">md") {
		margin-left: -$spacer*4;
	}
}

//Image
.content-project-image {

	img {
		transition: filter 250ms;
	}

	a {
		position: relative;
		display: block;

		&:hover {
			&:after {
				content: " ";
				position: absolute;
				top: 0%;
				left: 0;
				width: 100%;
				height: 100%;
				mix-blend-mode: screen;
				opacity: 0.15;
			}
			img {
				background: $blue;
				filter: grayscale(.25) brightness(1.05);
			}
		}
	}
}