.backdrop {
	&:after {
		content: " ";
		position: absolute;
		left: $spacer*1.5;
		bottom: -$spacer*1.5;
		width: 100%;
		height: 60%;

		mix-blend-mode: multiply;
		z-index: -1;
	}
}

.backdrop-orange:before,
.backdrop-orange:after {
	background: $orange;
}

.backdrop-blue:before,
.backdrop-blue:after {
	background: $blue;
}

.backdrop-purple:before,
.backdrop-purple:after {
	background: $purple;
}

.backdrop-custom:before,
.backdrop-custom:after {
	background: currentColor;
}

//Text Column
.backdrop.backdrop-text-col {
	&:after {
		width: auto;
		height: 100%;
		left: -$spacer;
		right: -$spacer;
		top: -$spacer;
		bottom: -$spacer;
	}	
}

//Donate Block
//
.backdrop.backdrop-donate-block:after {
	top: 0;
	bottom: 0;
	height: 100%;
	width: auto;
	left: -$spacer;
	right: -$spacer;
}

//Project Intro
.project-intro-slideshow .backdrop:after {
	//z-index: 1;
}

//Partners
//
.featured-partners.backdrop.backdrop-text-col {
	height: 90%;
}