.project-list {
	
	//Alt Coloring
	.project {

		&:nth-of-type(odd) {
			.content-project-header {
				@extend .offset-md-1;
			}
			
		}

		&:nth-of-type(1n) {

			.content-project-header-bg,
			.content-project-link {
				background: $purple;
			}

			.content-project-link:hover {
				background: $purple-light;
			}

			.content-project-image a:after {
				background: $purple-light;
			}
		}

		&:nth-of-type(2n) {

			.content-project-header-bg,
			.content-project-link {
				background: $orange;
			}

			.content-project-link:hover {
				background: $orange-light;
			}

			.content-project-image a:after {
				background: $orange-light;
			}

		}

		&:nth-of-type(3n) {

			.content-project-header-bg,
			.content-project-link {
				background: $blue;
			}

			.content-project-link:hover {
				background: $blue-light;
			}

			.content-project-image a:after {
				background: $blue-light;
			}

		}

	}
}