.site-banner {

	svg {
		//height: 40px;
		//width: 60px;
		height: 80px;
		width: 120px;
		position: absolute;
		top: -25px;

		path {
			transition: fill 250ms;
		}

		path[fill="#F60"] ~ path {
			transition: fill 500ms;
		}

	}

	.brand {

		&:hover {

			svg {
				
				path {
					fill: $gray-500;
				}
			}
		}

	}
}