.content-stat {
	vertical-align: top;
	position: relative;
	width: 300px;
}

//Fixed
.content-stat.stat-fixed {
	width: 220px;
	border-radius: 100%;

	.mb-2 {
		margin-bottom: $spacer !important;
		margin-top: $spacer;
	}

	//Colors
	//
	&:nth-of-type(1n) {
		background: $purple-light !important;
	}

	&:nth-of-type(2n) {
		background: $blue-light !important;
	}

	&:nth-of-type(3n) {
		background: $orange-light !important;
	}

}

//Percentages
.content-stat.stat-percent {
	background: $white !important;

	.stat-percent-number {
		position: absolute;
		top: 100px;
		left: 104px;
		font-size: 3rem !important;

		@include media(">md") {
			top: 50px;
		}

	}

	svg {
		width: 200px;
		
		@include media(">md") {
			margin-top: -45px;
		}

	}

	//Colors
	//
	&:nth-of-type(odd) {
		* {
			color: $purple-light !important;
		}

		path {
			stroke: $purple;
		}

		path:first-of-type() {
			stroke: lighten($purple-light, 20) !important;
		}
	}

	&:nth-of-type(even) {
		* {
			color: $blue-light;
		}

		path {
			stroke: $blue;
		}

		path:first-of-type() {
			stroke: lighten($blue-light, 20) !important;
		}
	}
}