.slider {
    width: 100%;
    height: 0;
    padding-bottom: 57%;
    overflow: hidden;

    .slides.slick-slider {
        position: inherit;
    }

    .slide {
        position: relative;
        display: inline-block;
        list-style: none;
        
        img {
            width: 100% !important;
            max-width: 100%;
            
        }

        a {
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
        }
    }

    &:hover {
        .slick-prev,
        .slick-next {
            opacity: 1;
        }
    }

}

/* Arrows */

.slick-prev,
.slick-next {
    position: absolute;
    display: block;

    top: auto;
    bottom: -$spacer*3;
    right: 0;

    height: auto;
    width: $spacer * 2;
    
    line-height: 1;
    font-size: $h4-font-size;
    
    cursor: pointer;
    
    color: $gray-700;
    background-color: transparent;

    padding: 0;
    border: none;
    outline: none;
    
    z-index: 10;
    
    &:hover, 
    &:focus, 
    &:active {
        outline: none;
    }

    &.slick-disabled:before {
        opacity: $slick-opacity-not-active;
    }
}

.slick-prev {
    right: $spacer * 3;
}

.slick-next {

}

/* Dots */

.slick-dotted.slick-slider {
    margin-bottom: 0px;
}

.slick-dots {
    position: absolute;
    bottom: $grid-gutter-width / 2;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 0px;
        padding: 0;
        cursor: pointer;
        button {
            border: 0;
            background: transparent;
            display: block;
            height: 20px;
            width: 20px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 5px;
            cursor: pointer;
            &:hover, &:focus {
                outline: none;
                &:before {
                    opacity: $slick-opacity-on-hover;
                }
            }
            &:before {
                position: absolute;
                top: 0;
                left: 0;
                content: $slick-dot-character;
                width: 20px;
                height: 20px;
                font-family: $slick-font-family;
                font-size: $slick-dot-size;
                line-height: 20px;
                text-align: center;
                color: $slick-dot-color;
                opacity: $slick-opacity-not-active;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
        }
        &.slick-active button:before {
            color: $slick-dot-color-active;
            opacity: $slick-opacity-default;
        }
    }
}


