//
// Color system
//

$white:  #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #868e96 !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black:  #000 !default;

$grays: (
  100: $gray-100,
  200: $gray-200,
  300: $gray-300,
  400: $gray-400,
  500: $gray-500,
  600: $gray-600,
  700: $gray-700,
  800: $gray-800,
  900: $gray-900
) !default;

$blue:    #2805ad !default;
$indigo:  #6610f2 !default;
$purple:  #6f42c1 !default;
$pink:    #e83e8c !default;
$red:     #dc3545 !default;
$orange:  #fd7e14 !default;
$yellow:  #ffc107 !default;
$green:   #28a745 !default;
$teal:    #20c997 !default;
$cyan:    #17a2b8 !default;

$blue:          #2805ad;
$blue-light:    #1e0add;
$orange:        #ff6600;
$orange-light:  #ff9600;
$purple:        #7c00ad;
$purple-light:  #930ad6;

$colors: (
  blue: $blue,
  indigo: $indigo,
  purple: $purple,
  pink: $pink,
  red: $red,
  orange: $orange,
  yellow: $yellow,
  green: $green,
  teal: $teal,
  cyan: $cyan,
  white: $white,
  gray: $gray-600,
  gray-dark: $gray-800,

  blue-light: $blue-light,
  orange-light: $orange-light,
  purple-light: $purple-light

) !default;

$theme-colors: (
  primary: $blue,
  secondary: $purple,
  success: $green,
  info: $cyan,
  warning: $orange,
  danger: $red,
  light: $gray-100,
  medium: $gray-600,
  dark: $gray-800,

  cyan: $cyan,

  //Custom
  blue: $blue,
  blue-light: $blue-light,
  purple: $purple,
  purple-light: $purple-light,
  orange: $orange,
  orange-light: $orange-light

) !default;

// Set a specific jump point for requesting color jumps
$theme-color-interval: 8% !default;


// Body
//
// Settings for the `<body>` element.

$body-bg:       $white !default;
$body-color:    $black !default;

// Links
//
// Style anchor elements.

$link-color:            theme-color("secondary") !default;
$link-decoration:       none !default;
$link-hover-color:      $blue-light !default;
$link-hover-decoration: none !default;
