body.login-action-login.wp-core-ui,
body.login-action-lostpassword.wp-core-ui {
	padding-top: 0;

	* {
		&:active,
		&:focus {
			outline: none !important;
		}
	}

	#login {
		width: 90%;
		max-width: 500px;

		//Messages
		.message {
			text-align: center;
			border-left: 0;
			margin-bottom: 0;
		}

	}

	#login_error {
		border-left: 0;
		color: $red;
		text-align: center;
	}


    #login h1 a, 
    .login h1 a {
	    background-image: url('../images/_svg/logo-full.svg');
		height:0;
		padding-bottom: 20%;
		width: 100%;
		background-size: contain;
		background-position: center center;
		background-repeat: no-repeat;
		z-index: 2;
		position: relative;
    }


	#loginform,
	#lostpasswordform,
	#resetpasswordform {
		position: relative;
		z-index: 1;
		//background: $white;
		border: 0;
		padding-top: 80px;
		box-shadow: none !important;

		label {
			display: inline-block;
			width: 100%;
			margin-bottom: $spacer*2;
		}

		input[type="text"],
		input[type="password"],
		input[type="checkbox"] {
			@extend .form-control;

			&:checked:before {

			}
		}


		input[type="checkbox"] {
			-webkit-appearance: none;
			width: 20px;
			height: 20px;
			border: 1px solid $blue;
			display: inline-block;
			vertical-align: middle;

			&:hover {
				cursor: pointer !important;
			}

			&:checked {
				background: $blue;

				&:before {
					content: none;
				}
			}

		}

		input[type="submit"] {
			@extend .btn;
			@extend .btn-primary;
			border-radius: 0 !important;
			text-transform: uppercase;
			box-shadow: none !important;
			text-shadow: none !important;
			height: auto !important;

			&:hover {
				height: auto !important;
			}
		}
	}

	
	p#nav {
		text-align: center;

		a:hover {

		}
	}

	#backtoblog {
		display: none;
	}

}